import React from 'react';

import VerticalNavbar from '../VerticalNavbar/VerticalNavbar';
import Particles from 'particlesjs/dist/particles.min';
import TextCarousel from '../TextCarousel/TextCarousel';

import '../../assets/css/texts.css';
import './Cover.css';

class Cover extends React.Component {
    componentDidMount() {
        Particles.init({
            selector: '.particles-background',
            color: ['#1E90FF', '#ffffff'],
            speed: .2,
            connectParticles: true,
            maxParticles: 150,
            minDistance: 80
        });
    }

    render() {
        return (
            <React.Fragment>
                <canvas className="particles-background"></canvas>
                <section id="cover">
                    <div>
                        <ul>
                            <li><h1>Hi,</h1></li>
                            <li><h1>I'm <strong>Lautaro Aguirre</strong>, a Software Developer</h1></li>
                            <li><h1>I
                                <TextCarousel texts={[
                                    'love to innovate',
                                    'am an autodidact',
                                    'am always learning',
                                    'am a problem solver',
                                    'take care of details',
                                    'love to create things',
                                    'write elegant code for humans',
                                ]}/>
                            </h1></li>
                        </ul>
                        <VerticalNavbar></VerticalNavbar>
                    </div>
                </section>
            </React.Fragment>

        );
    }
}

export default Cover;