import React from 'react';

import './VerticalNavbar.css';
import '../../assets/css/icons.css';

import linkedinLogo from '../../assets/icons/linkedin.png';
import githubLogo from '../../assets/icons/github.png';

function VerticalNavbar(props) {
    return (
        <nav id="vertical-navbar">
            <ul>
                <li><a href="https://linkedin.com/in/lautagui" target="_blank"><img src={linkedinLogo} className="icon" alt="LinkedIn"/></a></li>
                <li><a href="https://github.com/thisbar" target="_blank"><img src={githubLogo} className="icon" alt="GitHub"/></a></li>
            </ul>
        </nav>
    )
}

export default VerticalNavbar;
